.banner {
    background: #000;
    height: 100vh;
    position: relative;
    width: 100vw;
    background: url("../../images/Waves.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}



.loadng-full
{

margin-left:4vw;
}

.banner_info {

    height: 90%;
    max-height: 70%;
    position: relative;
    width: 100%;
    color: #fff;
     
    
    
}

.banner_search {
    display: flex;
    flex-direction: column;
    width:100vw;
    border-radius:12px;
    left:0;
}

.banner_searchButton {
    background-color: #fff !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    color: #ff7779 !important;
    font-size: 18px !important;
}

.banner_info > button {
    background-color: #ff7779;
    color: #fff;
    text-transform: inherit;
    margin-top: 20px;
    font-weight: 600;
   
}

.banner_info > button:hover {
    background-color: #fff;
    color: white;
   
}

.banner_info > h1 {
 
    padding-left: 1vw;
    padding-top: 150px;
    font-size:2.55rem;
}

.banner_info > h5 {
    margin-top: 10px;
    padding-left: 20px;
     font-size:2.5rem;
}


.btn-contact-mobile
{
  display:none;
  position:relative;
  }
  
  


@media screen and (max-width: 1500px) {
    .banner_info {
        padding-top: 18vh;
        height: 55%;
    }
}

@media screen and (max-width: 978px) {
    .banner_info {
        padding-top: 18vh;
        height: 55%;
    }

    .banner_info > h1 {
      font-size: 25px;
    }

    .banner_info > h5{
    font-size: 15px;
    }

    .banner_info > button {
    font-size: 13px;
    }   
}

@media screen and (max-width: 840px) {
    .banner_info {
        padding-top: 19vh;
        height: 53%;
    }

    .banner_info > h1 {
      font-size: 23px;
    }

    .banner_info > h5{
    font-size: 14px;
    }

    .banner_info > button {
        font-size: 11px;
    }   
}

@media screen and (max-width: 700px) {
.banner {
    background: #000;
    height: 150vh;
    position: relative;
    width: 100vw;
    background: url("../../images/Waves.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}

    .banner_info {
        padding-top: 18vh;
        height: 50%;
        width:100%;
    }

    .banner_info > h1 {
      font-size: 20px;
    }

    .banner_info > h5{
    font-size: 13px;
    }

    .banner_info > button {
        font-size: 10px;
    }   
    
.btn-contact-mobile
{
  display:block;
  position:fixed;
  bottom:0;
  height:10vh;
  width:100vw;
  }
  
.contact-mob
{
   margin-left:-2vw;
   bottom:20vh;
   position:fixed;
   z-index:2;
   transition: 2s ease-in-out;
}

.loan-calc
{
  width:2vw;
  height:10vh;
}

.mob-contact
{
  width:100vw;
  position:sticky;
}


}

@media screen and (max-width: 605px) {

 
    
    .banner {
  width: 100vw;
  height: 60vh;
  object-fit: cover;
  position:relative;
  left: 0;
  right: 0;
  top: 30vh;
  bottom: 0;

    
    }
    .banner_info {
        padding-top: 23vh;
        height: 10%;
        margin-top:-49vh;
    }

    .banner_info > h1 {
      font-size: 1.5rem;
    }

    .banner_info > h5{
    font-size: 1rem;
    }

    .banner_info > button {
        font-size: 8px;
    }   
}

@media screen and (max-width: 450px) {
    .banner_info {
        padding-top: 10vh;
        height: 65%;
    }

    .banner_info > h1 {
      font-size: 20x;
    }

    .banner_info > h5{
    font-size: 12px;
    }

    .banner_info > button {
        font-size: 11px;
    }   
}
